import {useState} from 'react'
import {Collapse} from 'react-bootstrap'
import BiChevronDown from "../../../Assets/react-icons/BiChevronDown.svg"
import BiChevronUp from "../../../Assets/react-icons/BiChevronUp.svg"

interface FaqItemProps {
  question: string
  content: string
}

const FaqItem = ({question, content}: FaqItemProps) => {
  const [open, setOpen] = useState(false)
  return (
    <div className='faq_containet'>
      <div
        className='faq_item'
        onClick={() => setOpen(!open)}
        aria-controls='example-collapse-text'
        aria-expanded={open}
      >
        <div>
          <h6>{question}</h6>
        </div>
        <div>{open ? <img src={BiChevronUp} alt = "BiChevronUp" /> : <img src={BiChevronDown} alt="BichevronDown" />}</div>
      </div>
      <Collapse in={open}>
        <div id='example-collapse-text'>
          <p className='mt-3'>{content}</p>
        </div>
      </Collapse>
    </div>
  )
}

export default FaqItem
