import {Container, Row, Col} from 'react-bootstrap'
import FaqItem from './FaqItem'
import {v4 as uuid} from 'uuid'

const Faq = () => {
  const faqItems = [
    {
      id: uuid(),
      question: 'What is an NFT?',
      answer:
        'NFTs or non-fungible tokens, are cryptographic assets on blockchain with unique identification codes and metadata that distinguish them from each other. NFTs are unique and not mutually interchangeable, which means no two NFTs are the same. NFTs can be a unique digital artwork, sneaker in a limited-run fashion line, in-game item, digital collectible etc. ',
    },
    {
      id: uuid(),
      question: 'Customer support is available ? ',
      answer: ` You can fill out a request for support via email Please provide:
            Your email address
            Your wallet address (if applicable)
            Choose relevant categories
            Provide detailed information regarding your inquiry.
        `,
    },
    {
      id: uuid(),
      question: 'How do I find my transaction hash? ',
      answer: `Unicus staff often requires a "transaction hash" from Etherscan or Polygonscan to troubleshoot support issues.
        Transaction hashes are unique IDs recording each transaction on the blockchain, this includes NFT purchases, sales or even cancelling orders. All gas fees paid will generate a transaction hash.`,
    },
    {
      id: uuid(),
      question: 'What are gas fees on Unicus?',
      answer: `Gas fees are like transaction fees on the Ethereum blockchain. When you make transactions, such as transferring crypto to another wallet or purchasing an NFT on Unicus, you'll need enough ETH in your wallet for the initial transaction and the associated gas fees. `,
    },
  ]
  return (
    <div className='faq'>
      <div className='section_info'>
        <p className='section_small_heading'>have a question</p>
        <h1 className='section_heading'>Frequently asked questions</h1>
      </div>
      <Container>
        <div className='faq__container'>
          <Row>
            {faqItems?.map((item) => (
              <Col xl={12} key={item.id}>
                <FaqItem question={item.question} content={item.answer} />
              </Col>
            ))}
          </Row>
        </div>
      </Container>
    </div>
  )
}

export default Faq
